.openuptip[tooltip][flow^="up"]::before {
  bottom: 100%;
  border-bottom-width: 0;
  border-top-color: var(--color-primary);
}
.openuptip[tooltip]:not([flow])::after,
.openuptip[tooltip][flow^="up"]::after {
  bottom: calc(100% + 5px);
}
.openuptip[tooltip]:not([flow])::before,
.openuptip[tooltip]:not([flow])::after,
.openuptip[tooltip][flow^="up"]::before,
.openuptip[tooltip][flow^="up"]::after {
  left: 50%;
  transform: translate(-50%, -0.5em);
}
@keyframes tooltips-vert {
  to {
    opacity: 0.9;
    transform: translate(-50%, 0);
  }
}
@keyframes tooltips-horz {
  to {
    opacity: 0.9;
    transform: translate(0, -50%);
  }
}
.openuptip[tooltip]:not([flow]):hover::before,
.openuptip[tooltip]:not([flow]):hover::after,
.openuptip[tooltip][flow^="up"]:hover::before,
.openuptip[tooltip][flow^="up"]:hover::after,
