/* Default (light) theme styles */
body {
  background-color: white;
  color: black;
}

/* Dark theme styles */
[data-theme='dark'] body {
  background-color: black;
  color: white;
}
.chatbot .main-center-content-m-left.center-content .search-form {
  max-width: calc(100% - 691px);
}
.Azure{
  font-weight: bold;
}
/* ChatBot.css */
[data-theme="dark"]
.bot-response p{
  font-size: 16px; /* Adjust this value to increase or decrease the font size */
  color: #f8f8f8;
}
.bot-response pre {
  font-size: 16px;
  color: #fcfcfc;
  background-color: #333333;
  padding: 10px;
  border-radius: 5px;
  overflow: auto; /* Ensures that long code blocks are scrollable */
}

.bot-response code {
  font-family: monospace;
}


[data-theme="light"]
.bot-response p{
  font-size: 16px; /* Adjust this value to increase or decrease the font size */
  color: #141414;
}
.bot-response pre {
  font-size: 16px;
  color: #fcfcfc;
  background-color: #333333;
  padding: 10px;
  border-radius: 5px;
  overflow: auto; /* Ensures that long code blocks are scrollable */
}

.bot-response code {
  font-family: monospace;
}
